$ss-font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif !default;
$ss-white: #fff !default;
$ss-black: #000 !default;
$ss-blue: #209cee !default;
$ss-green: #23d160 !default;
$ss-light-gray: #c7c7c7 !default;
$ss-dark-gray: #4a4a4a !default;
$ss-yellow: #ffdd57 !default;
$ss-red: #ff3860 !default;
$ss-width: 350px !default;

$ss-default: $ss-light-gray !default;
$ss-success: $ss-green !default;
$ss-info: $ss-blue !default;
$ss-warning: $ss-yellow !default;
$ss-danger: $ss-red !default;

$ss-colors: ("default": $ss-default, "success": $ss-success, "info": $ss-info, "warning": $ss-warning, "danger": $ss-danger) !default;

@mixin button {
    -webkit-appearance: button;
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
    overflow: visible;
    padding: 0;
    text-transform: none; }

* {
    box-sizing: border-box;

    &::after,
    &::before {
        box-sizing: border-box; } }

.ss-snackbars {
    display: none;
    position: fixed;
    right: 1rem;
    top: 1rem;
    width: $ss-width;

    &.ss-snackbars-active {
        display: block; } }

.ss-snackbar-icon {
    align-items: center;
    display: flex;
    justify-content: center;
    padding-right: 1rem; }

.ss-icon {
    color: rgba($ss-black, .4);
    display: inline-flex;
    height: 1rem;
    width: 1rem;

    svg {
        height: 100%;
        width: 100%; } }

.ss-close {
    @include button;
    align-items: center;
    display: inline-flex;
    justify-content: center;
    margin: -1.5rem -1rem;
    padding: 1.5rem 1rem;

    &:hover .ss-icon {
        color: rgba($ss-black, .8); } }

.ss-snackbar {
    backdrop-filter: blur(2px);
    background-color: rgba($ss-white, .9);
    border-left-style: solid;
    border-left-width: 2px;
    box-shadow: 0 1px 4px rgba($ss-black, .1), 0 0 6px rgba($ss-black, .04);
    color: $ss-dark-gray;
    display: flex;
    font-family: $ss-font-family;
    padding: 1.5rem 1rem;
    pointer-events: auto;
    transform: translate3d(110%, 0, 0);
    visibility: hidden;

    &:not(:last-child) {
        margin-bottom: 1rem; }

    &.ss-snackbar-active {
        transform: translate3d(0, 0, 0);
        visibility: visible; }

    @each $name, $color in $ss-colors {
        &.ss-snackbar-#{$name} {
            border-left-color: $color;

            .ss-snackbar-icon .ss-icon {
                color: $color; } } } }

.ss-snackbar-body {
    flex-grow: 1; }
